import React from "react";
import "./style.css";
import InfoIcon from "@material-ui/icons/Info";
import LocationOnIcon from "@material-ui/icons/LocationOn";
import ContactMailIcon from "@material-ui/icons/ContactMail";
import LocalOfferIcon from "@material-ui/icons/LocalOffer";

class Menu extends React.Component {
  render() {
    return (
      <div id="navbar" className="ui segment">
        <div className="link">
          <b>wypoczynkowe.rewa.pl</b>
        </div>
        <div>
          <a href="#about">
            <div>
              <InfoIcon className="icon" fontSize="small" /> o nas
            </div>
          </a>

          <a href="#photos">
            <div>
              <LocalOfferIcon className="icon" fontSize="small" /> oferta
            </div>
          </a>

          <a href="#contact">
            <div>
              <ContactMailIcon className="icon" fontSize="small" /> kontakt
            </div>
          </a>

          <a href="#geo">
            <div>
              <LocationOnIcon className="icon" fontSize="small" /> dojazd
            </div>
          </a>
        </div>
      </div>
    );
  }
}

export default Menu;
