import React from "react";
import "./style.css";
class About extends React.Component {
  render() {
    return (
      <div id="about">
        <div id="main">
          <div className="main-1">
            <div className="block" id="desc">
              <div className="title">O nas:</div>
              <div className="image"></div>
              <div className="description">
                Jesteśmy rodzinnym gospodarstwem. Zajmujemy się wynajmowaniem
                domków letnich w sezonie.
              </div>
            </div>
            <div className="block" id="b1"></div>
          </div>
        </div>
      </div>
    );
  }
}
export default About;
