import React from "react";
import "./style.css";

class Contact extends React.Component {
  handleSend = () => {
    let title = document.getElementById("title-mailer").value;
    let body = document.getElementById("body-mailer").value;

    window.open(
      `mailto:wypoczynkowe.rewa@gmail.com?subject=${title}&body=${body}`
    );
  };

  render() {
    return (
      <div id="contact" className="ui segment">
        <div className="title">Kontakt:</div>
        <div className="ui two column very relaxed grid columns">
          <div className="column cl1">
            <div className="ui form">
              <div className="field">
                <label>Tytuł wiadomości</label>
                <input
                  id="title-mailer"
                  type="email"
                  placeholder="Tytuł wiadomości"
                />
              </div>

              <div className="field">
                <label>Treść wiadomości</label>
                <textarea id="body-mailer" />
              </div>
            </div>

            <div className="ui blue submit button" onClick={this.handleSend}>
              Wyślij
            </div>
          </div>

          <div className="middle aligned column cl2">
            <div className="button2">
              <i className="phone icon"></i>
              <div>000-000-000</div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Contact;
