import React from "react";
import ImageGallery from "react-image-gallery";
import "../../../node_modules/react-image-gallery/styles/css/image-gallery.css";
import "./style.css";
import data from "../../data/houses";
import PersonIcon from "@material-ui/icons/Person";
import nextId from "react-id-generator";

class Photos extends React.Component {
  state = {
    all: data
  };

  renderPeople = () => {
    return <PersonIcon key={nextId()} />;
  };

  rep = x => {
    let test = [];

    while (x > 1) {
      x--;
      test.push(this.renderPeople());
    }

    if (x === 1) {
      test.push(this.renderPeople());
    }

    /*
    return test.map(element => {
      return element;
    });
    */

    return (
      <div className="person">
        {test.length} os.
        {this.renderPeople()}
      </div>
    );
  };

  renderAll = () => {
    return this.state.all.map((element, index) => {
      let photos = [];
      let cl = "";
      let el = "";

      if (index % 2 === 0) {
        cl = "main main-1";
        el = "main el-1";
      } else {
        cl = "main main-2";
        el = "main el-2";
      }

      if (window.innerWidth < 451) {
        photos = element.device;
      } else {
        photos = element.original;
      }
      return (
        <div className="house ui card" key={nextId()}>
          <div className="content">
            <div className="image">
              <ImageGallery items={photos} key={nextId()} />
            </div>
            <div className="header">{element.title}</div>

            <div className="date">{this.rep(element.people)}</div>

            <div className="meta">{element.costs}</div>

            <div className="description">{element.description}</div>
          </div>
        </div>
      );
    });
  };

  render() {
    return (
      <div id="photos">
        <div id="gallery">
          <div className="title">Nasza oferta:</div>
          {this.renderAll()}
        </div>
      </div>
    );
  }
}

export default Photos;
